
.divAutocomplete {
  position: relative;
}
.autocomplete {
  display: none;
  width: 100%;
  max-height: 250px;
  overflow-y: scroll;
  z-index: 1;
  position: absolute;
  background-color: white;
  left: -1px;
  padding-top: 10px;
  border-bottom: 1px solid #ddd;
  border-left: 1px solid #ddd;
  border-right: 1px solid #ddd;
}
.item-place {
  height: 30px;
  border-bottom: 1px solid grey;
  margin-bottom: 5px;
  overflow: hidden;
  &__img {
    width: 20px;
    height: 20px;
  }
  &__title {
    padding-left: 10px;
    font-size: 13px;
  }
}
